.FrequencyControls .slider {
  width: 100%;
}

.FrequencyControls .slider {
  -webkit-appearance: none;
  width: 100%;
  height: 4px;
  border-radius: 5px;   
  background: var(--dark);
  outline: none;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  -o-transition: opacity .2s;
  transition: opacity .2s;
  cursor: pointer;
}

.FrequencyControls .slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 23px;
  height: 23px;
  border-radius: 50%; 
  background: var(--lighter);
}

.FrequencyControls .slider::-moz-range-thumb {
  width: 23px;
  height: 23px;
  border-radius: 50%;
  background: var(--lighter);
}


